import React from 'react'
import ProductDataDashBoard from './ProductDataDashboard';
import { cookies } from 'next/headers';
import prisma from '../../../../prisma/client';
import { Id } from '@/lib/manage/types';
import { User } from '@/lib/manage/user';
import ProductBuilder from './ProductBuilder';

export default async function FormPage() {
  const sess = (await cookies()).get('session-us')?.value;
  if (sess == null)
    return <></>
  let usr = await prisma.user.findFirst({
    where: {
      sessions: {
        some: {
          sessionToken: sess
        }
      }
    }
  });
  let ds = "";
  await User.getAccessibleEntities(usr.id as Id<'User'>);
  let forms = await Form.listForms(usr.isSuperAdmin ? undefined : usr.id as Id<'User'>);

  return (
    <div>
      <ProductBuilder schema={ds} id={usr.id} />
      <ProductDataDashBoard schema={ds} forms={forms} />
    </div>
  )
}
